.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Header.js */
.is-current a {
  text-shadow: 0 0 5px #ffa500;
  
}

/* Style for small screens */
@media only screen and (max-width: 600px) {
  body {
    background-color: #fff; /* ocean blue */
  }

  /* Header */
  .header {
    padding: 10px;
    text-align: center;
  }

  /* Company Mission */
  .company-mission {
    padding: 10px;
  }

  /* Service Details */
  .service-details {
    padding: 10px;
  }

  /* Hosting Packages */
  .hosting-packages {
    padding: 10px;
  }

  /* FAQ */
  .faq {
    padding: 10px;
  }

  /* Contact Us */
  .contact-us {
    padding: 10px;
  }

  /* Footer */
  .footer {
    padding: 10px;
    text-align: center;
  }
}

/* Style for medium and large screens */
@media only screen and (min-width: 601px) {
  body {
    background-color: #fff; /* ocean blue */
  }

  /* Header */
  .header {
    padding: 20px;
    text-align: center;
  }

  /* Company Mission */
  .company-mission {
    padding: 20px;
  }

  /* Service Details */
  .service-details {
    padding: 20px;
  }

  /* Hosting Packages */
  .hosting-packages {
    padding: 20px;
  }

  /* FAQ */
  .faq {
    padding: 20px;
  }

  /* Contact Us */
  .contact-us {
    padding: 20px;
  }

  /* Footer */
  .footer {
    padding: 20px;
    text-align: center;
  }
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
