/* HEADER STYLING */
header {
    background-color: #36414E;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 1;
  }
  
  .logo {
    width: 180px;
    cursor: pointer;
  }
  
  .nav-links {
    display: none;
    list-style: none;
    padding: 0;
    transition: all 0.3s ease-in-out;
  }
  
  .nav-links li {
    padding: 10px 0;
  }
  
  .nav-links li a {
    color: white;
    text-decoration: none;
    transition: color 0.3s ease-in-out;
  }
  
  .nav-links li a:hover {
    color: #ffa500;
  }
  
  .menu-button {
    display: block;
    cursor: pointer;
    font-size: 2rem;
    background-color: transparent;
    border: 2px solid transparent;
  }

  .hamburger-icon {
    width: 2rem;
    height: 2rem;
    color: #fff;
    cursor: pointer;
  }
  
  
  /* Style for medium and large screens */
  @media only screen and (min-width: 600px) {
    header {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  
    .nav-links {
      display: flex;
      align-items: center;
    }
  
    .nav-links li {
      margin: 0 10px;
      cursor: pointer;
    }
  
    .menu-button {
      display: none;
    }
  }
  
  /* Style for small screens */
  @media only screen and (max-width: 599px) {
    header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  
    .nav-links.open {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 80px;
      left: 0;
      right: 0;
      background-color: #36414E;
      padding: 20px;
    }
  }
  

/* Company Mission */

#companyMission {
    background-color: #F7F7F7;
    color: #000;
    padding: 20px;
  }
  
  .companyMission__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .companyMission__image {
    max-width: 100%;
    margin-bottom: 20px;
  }
  
  .companyMission__content {
    max-width: 60%;
    text-align: center;
  }
  
  @media only screen and (min-width: 600px) {
    .companyMission__wrapper {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
  
    .companyMission__image {
      max-width: 40%;
      margin-right: 20px;
      margin-bottom: 0;
    }
  
    .companyMission__content {
      max-width: 70%;
      text-align: left;
    }
  }

/* Service Details */

  #serviceDetails {
    background-color: #fff;
    color: #000;
    font-size: 18px;
    padding: 20px;
  }
  
  .serviceDetails__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .serviceDetails__content {
    max-width: 80%;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .serviceDetails__image {
    max-width: 100%;
  }

  .word {
    animation: fade-in-out 2s linear infinite;
    display: inline-block;
  }
  
  @keyframes fade-in-out {
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  
  @media only screen and (min-width: 600px) {
    .serviceDetails__wrapper {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }
  
    .serviceDetails__content {
      max-width: 70%;
      text-align: left;
      margin-bottom: 0;
      margin-right: 20px;
    }
  
    .serviceDetails__image {
      max-width: 40%;
    }
  }

/* Hosting Packages */

#hostingPackages {
    background-color: #F7F7F7;
    color: #000;
    padding: 20px;
  }
  
  .hostingPackages__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Center vertically and horizontally */
  }
  
  .hostingPackages__column {
    max-width: 80%;
    height: 420px;
    margin-bottom: 20px;
    background-color: transparent;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .package__button_wrapper {
    padding-top: 12px;
    width: 100%;
  }

  .package__button {
    margin-top: auto;
    width: 50%;
    height: 30px;
    color: #fff;
    font-weight: 700;
    background-color: #36414E;
    border: 1px solid #fff;
    border-radius: 5px;
  }

  .package__button:hover {
    cursor: pointer;
    background-color: #36414E;
    color: #fff;
  }
  
  @media only screen and (min-width: 600px) {
    .hostingPackages__wrapper {
      flex-direction: row;
      justify-content: space-between;
    }
  
    .hostingPackages__column {
      max-width: 30%;
      margin-bottom: 0;
      margin-right: 20px;
    }
  }
  
  
  
  /* Packages */

  .package {
    background-color: #fff;
    text-align: center;
    padding: 20px;
    width: 270px;
    height: 420px;
  }
  
  .package ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Center list items vertically */
  }
  
  .package ul li {
    margin-bottom: 5px;
    text-align: left; /* Center list items horizontally */
    padding-bottom: 5px;
  }

  .package ul li::before {
    content: '✔'; /* Use ✔ for checkmarks, or '−' for minus signs */
    margin-right: 5px;
  }


/* FAQ */

.faq-list {
    padding: 0 80px 20px 80px;
  }
  
.faq-list dt {
font-weight: bold;
color: #000;
text-align: left;
padding-bottom: 20px;
}
  
.faq-list dd {
color: #000;
text-align: left;
padding-bottom: 20px;
}

.load-more {
    font-size: 20px;
    padding: 10px 20px;
    background-color: transparent;
    border: 1px solid #fff;
    cursor: pointer;
  }
  
  .visible {
    opacity: 1;
    transition: opacity 0.3s;
  }
  
  .preview {
    opacity: 0.05;
    transition: opacity 0.3s;
  }
  
/* Contact Us */

#contactUs {
    background-color: #F7F7F7;
    padding: 20px;
  }

/* Footer */

footer {
    background-color: #f5f5f5;
    padding: 20px;
  }
  
  .footer__container {
    display: flex;
    flex-direction: column;
  }
  
  .footer__left,
  .footer__right {
    width: 100%;
  }
  
  @media (min-width: 768px) {
    .footer__container {
      flex-direction: row;
    }
  
    .footer__left {
      width: 60%;
    }
  
    .footer__right {
      width: 40%;
    }
  }
  
  .footer__right p {
    margin-bottom: 10px;
  }
  
  .footer__images {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
  }
  
  .footer__images img {
    max-width: 50px;
    height: auto;
  }
  
    